<template>
  <div class="deal-container">
    <!--<div class="choose-bg">-->
      <!--<img src="../assets/images/page_bg@2x.png" />-->
    <!--</div>-->
    <div class="deal-content">
      <div class="logo-img"><img src="../assets/images/logo@2x.png" /></div>
      <div>
        <div class="people-img" ref="box">
          <img :src="secondImg" alt="分享背景图">
          <!-- <div class="model-img"><img src="../assets/images/sign.png" /></div> -->
        </div>
      </div>
    </div>
    <div class="choose-button">
      <button @click="goBack">上一步</button>
      <button @click="dealPhoto">下一步</button>
    </div>
    <!-- <div @click="testImg">生成图片</div> -->
  </div>
</template>

<script>
export default {
  name: 'DealPhoto',
  data () {
    return {
      secondImg: ''
    }
  },
  watch: {
    // imgUrl (val, oldval) {
    //   this.$refs.box.style.display = 'none'
    //   this.$refs.sharebox.style.display = ''
    // }
  },
  mounted () {
    var firstImg = window.localStorage.getItem('firstImg')
    if (!firstImg) {
      return
    }
    this.secondImg = firstImg
  },
  methods: {
    goBack () {
      this.$router.go(-1)
    },
    dealPhoto () {
      this.$router.push('/sharephoto')
    }
  }
}
</script>

<style scoped lang='scss'>
.deal-container {
    width: 100%;
    background-image: url(../assets/images/page_bg@2x.jpg);
    background-size: 100%;
    background-repeat: no-repeat;
    overflow: hidden;
    padding-top: 3rem;
    box-sizing: border-box;
  .deal-content {
    .logo-img {
      text-align: center;
      width: 100%;
      margin-bottom: 2rem;
      img {
        width: 150px;
      }
    }
    .people-img {
      margin-top: 2rem;
      width: calc(100% - 8rem);
      margin: 0 4rem;
      img {
        width: 100%;
        height: 100%;
      }
      .model-img {
        width: 100%;
        position: relative;
        top: -10.5rem;
        right: 1.5rem;
        line-height: 0;
        img {
          width: 5rem;
          height: 5rem;
          position: absolute;
          top: 0;
          right: 0;
          z-index: 10;
        }
      }
    }
    // .share-img {
    //   display: none;
    //   width: 100%;
    //   line-height: 0;
    //   img {
    //     width: 100%;
    //   }
    // }
  }
  .choose-button {
    margin: 2rem 3rem;
    text-align: center;
    width: calc(100% - 6rem);
    display: flex;
    button {
      display: inline-block;
      margin: 0 10px;
      outline: none;
    }
  }
}
</style>



